import {Component, OnInit, ViewChild, ElementRef, EventEmitter, Input, Output, AfterViewChecked}  from '@angular/core';
import { SocketIoService , UserService }                                          from '../../providers';
import { Message }                                                                from '../../models/message';
import { User }                                                                   from '../../models/user';

@Component({
  selector: 'my-chat',
  template: require('./chat.component.html'),
  styles: [require('./chat.component.scss')]
})
export class ChatComponent implements AfterViewChecked {
  @ViewChild('scrollMe') private myScrollContainer: ElementRef;
  messages: Array<any> = [];
  draftMessageText: string;
  private user: User;

  constructor(private messageService: SocketIoService, private userService: UserService) {
    this.user = userService.getCurrentUser();
    this.messageService.newMessage.subscribe( m => {
        if (m != null) {
          console.log(m);
          m.isLoading = false;
          this.messages.push(m);
        }
      }, (e) => console.log(e)
    );
  }
  onEnter(event: any): void {
    this.sendMessage();
    event.preventDefault();
  }
  sendMessage(): void {
    this.user = this.userService.getCurrentUser();
    let m = new Message({
      username: this.user.username,
      datetime: new Date().toISOString(),
      isLoading: true,
      message: this.draftMessageText
    });
    this.draftMessageText = '';
    this.messages.push(m);
    this.messageService.sendMessage(m);
  }
  ngAfterViewChecked() {
    this.scrollToBottom();
  }

  scrollToBottom(): void {
    try {
      this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch (err) {
      console.log(err);
    }
  }
}

@Component({
  selector: 'my-message-row',
  styles: [require('./message-row.component.scss')],
  template: require('./message-row.component.html')
})
export class MessageRowComponent implements OnInit {
  @Input() message: Message;
  @Output() change = new EventEmitter<any>();
  messageMode: number;
  timeAgo: string;
  user: User;
  constructor(private messageService: SocketIoService, private userService: UserService) {
    this.messageService.receivedMessage
      .delay(1000).subscribe(msgId => {
      if (this.message.id === msgId) {
        this.message.isLoading = false;
      }
    });
  }
  ngOnInit() {
    this.messageMode = this.userService.getCurrentUser().username === this.message.username ? 1 : 0;
    this.timeAgo = this.calcTimeAgo(this.message.datetime);
    setInterval(() => {
      this.timeAgo = this.calcTimeAgo(this.message.datetime);
    }, 30000);
  }
  calcTimeAgo(messageTime: string): string {
    let today = new Date();
    let diffMs = Math.abs(new Date(messageTime.toString()).getTime() - today.getTime()); // milliseconds between now & Christmas
    let diffDays = Math.round(diffMs / 86400000); // days
    if (diffDays >= 7) {
      let weekCount = Math.floor(diffDays / 7);
      if (weekCount >= 4) {
        let monthCount = Math.floor(weekCount / 4);
        if (monthCount === 1) {
          return '1 month ago';
        } else {
          return monthCount + ' months ago';
        }
      } else if (weekCount < 4 && weekCount > 1) {
        return weekCount + ' weeks ago';
      } else {
        return '1 week ago';
      }
    } else if (diffDays < 7 && diffDays > 1) {
      return diffDays + ' days ago';
    } else if (diffDays === 1) {
      return '1 day ago';
    } else {
      let diffHrs = Math.round((diffMs % 86400000) / 3600000); // hours
      if (diffHrs > 1) {
        return diffHrs + 'hours ago';
      } else if (diffHrs === 1) {
        return '1 hour ago';
      } else {
        let diffMins = Math.round(((diffMs % 86400000) % 3600000) / 60000); // minutes
        if (diffMins > 1) {
          return diffMins + ' minutes ago';
        } else if (diffMins === 1) {
          return '1 minute ago';
        } else {
          let diffSeconds = Math.round(diffMs / 1000);
          if (diffMins >= 3) {
            return diffSeconds + ' seconds ago';
          } else {
            return 'a moment ago';
          }
        }
      }
    }
  }
}



